import React from "react";
import { useForm } from "react-hook-form";
import { createFields } from "../../../common/formsControls/formControls";
import { useNavigate } from "react-router-dom";
import css from './profileDataForm.module.css'

const ProfileDataForm = ({ saveProfile, goToEditMode, profile, error, addError }) => {

    const { fullName, lookingForAJob, lookingForAJobDescription, aboutMe,
        contacts: { facebook, website, vk, twitter, instagram, youtube, github, mainLink, } } = profile

    const defaultValues = {
        fullName,
        lookingForAJob,
        lookingForAJobDescription,
        aboutMe,
        contacts: { facebook, website, vk, twitter, instagram, youtube, github, mainLink, }
    }

    const {
        register,
        handleSubmit,
        setValue,
        watch,
        formState: { errors },
    } = useForm({
        mode: 'onChange',
        defaultValues,
    })

    const navigate = useNavigate();

    const onSubmit = (formData) => {
        saveProfile(formData);
        if (error) {
            addError(null)
        }
        if (error) {
            navigate(-2)
        }
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)} >

            <div className={css.input}>Name: {createFields("input", "", "", register, "fullName", " ", 20, "  20 ")}</div>
            <div>{errors.fullName && <p className={css.formSummeryError}>{errors.fullName.message || 'error'}</p>}</div>

            <div className={css.flex}><div >Looking for a job:</div>{createFields("input", "checkbox", "", register, "lookingForAJob")} </div>

            <div className={css.textarea}>My professional skills: {createFields('textarea', "", "", register, "lookingForAJobDescription")}</div>
            <div className={css.textarea}>About me: {createFields('textarea', "", "", register, "aboutMe")}</div>

            <div>
                {Object.keys(profile.contacts).map(key => {
                    return <div className={css.input} key={key}> {key}: {createFields("input", "", "", register, `contacts.${key}`)}</div> //!    - `contacts.${key}`
                })}
            </div>

            <button onClick={() => setValue(watch("fullName", "lookingForAJob", "lookingForAJobDescription", "aboutMe", "facebook", "website", "vk", "twitter", "instagram", "youtube", "github", "mainLink"))}></button>

            {error && <div className={css.formSummeryError}>{error}</div>}

        </form >
    )
}

export default ProfileDataForm;