import React, { useEffect, useState } from "react";

const ProfileStatusWithHooks = ({ status, updateStatus }) => {

    let [editMode, setEditMode] = useState(false);
    let [newStatus, setStatus] = useState(status)  //  ,       

    useEffect(() => {                                //      
        setStatus(status);
    }, [status])

    const activateEditMode = () => { //     
        setEditMode(true)
    }

    const deActiveEditMode = () => {  //     
        setEditMode(false)
        updateStatus(newStatus)
    }

    const onStatusChange = (event) => { //        state
        setStatus(event.currentTarget.value)
    }

    return (
        <div>
            {!editMode &&     //  editMode  false,  <div
                <div onClick={activateEditMode}>{status || ' '}</div>
            }
            {editMode &&      //  editMode  true,  <input
                <div>
                    <input
                        autoFocus
                        onChange={onStatusChange}  //        state
                        onBlur={deActiveEditMode}  //      (  )
                        value={newStatus}          //      
                    />
                </div>
            }
        </div>
    )
}

export default ProfileStatusWithHooks; 